.todoForm {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90%;
    margin: 20px auto;
    border: solid 3px gray;
    border-radius: 10px;
    padding: 5px;
}

.toggleFields {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.toggleFields label {
    color: gray;
    font-weight: 600;
    font-size: 18px;
}

.todoToggle {
    height: 15px;
    width: 15px;
}

.todoContent {
    border: 0;
    outline: none;
    border-bottom: solid 2px grey;
    width: 50%;
    min-width: 120px;
}


